.slide-container {
  height: 100%;
  width: 70%;
  margin: auto; }

h3 {
  text-align: center; }

.each-slide > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 100vh;
}

.each-slide span {
  padding: 20px;
  font-size: 40px;
  font-style: italic;
  color: #ffffff;
  background: rgba(0,0,0,0.2);
  border-radius: 6px;
  text-align: center;
}

.each-fade {
  display: flex;
}

.each-fade .image-container {
  width: 75%;
  overflow: hidden;
}

.each-fade .image-container img {
  width: 100%;
}

.each-fade h2 {
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  background: #adceed;
}

.react-slideshow-container + div.indicators{
  position: relative;
  top: -13px;
  height: 0;
  margin-top: 0 !important;
}